import React from 'react'
import qrData from './qrData'
import { Link } from 'react-router-dom'

const Qr = () => {
    const language = 'es'
    const { day, night, pedidos } = qrData
    const cartas = [day, night, pedidos]
    const button = {
        es: 'Ver Carta',
        cat: ''
    }

    return (
        <div className='qr'>
            <div className='logo' >
                <Link to='/' />
            </div>
            <div className='cartas'>
                {cartas.map((carta) => (
                    <div className='carta'>
                        <div className='carta-bg'>
                            <div className='carta-title'>{carta[language]}</div>
                            <div className='carta-button'>
                                <a href={carta.pdf} target='blank' >
                                    {button[language]}
                                </a>
                            </div>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    )
}

export default Qr